@import url(//fonts.googleapis.com/css?family=Work+Sans:300,400,600);
@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
/* common.css */

/* Обнуляющие стили -------------------------------------------------------------------- */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: sans-serif;
	line-height: 1;
}

h1, h2, h3, h4, h5, h6 {
	font-size: inherit;
	font-weight: inherit;
}

ul, ol {
	list-style-type: none;
}

a {
	text-decoration: none;
	cursor: pointer;
}

button {
	font-size: inherit;

	border: none;
	background-color: transparent;
	cursor: pointer;
}

img {
	vertical-align: top;
}
/* ------------------------------------------------------------------------------------- */

/* Технические классы ------------------------------------------------------------------ */

._container {
	max-width: 1200px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
}

/* Для адаптивного вывода изображений через тег <img> */
._ibg {
	position: relative;
	/* padding-bottom: n%; */ /* Регулирует высоту изображения. */
}

._ibg img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

._hidden {
	display: none;
}

/* Как правило, текст для скринридеров, который не нужно выводить. */
._visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
}
/* ------------------------------------------------------------------------------------- */
/* app.css */

html {
	scroll-behavior: smooth;
}

/* header ------------------------------------------------------------------------------ */

.header__body {
	padding-top: 20px;
	padding-bottom: 20px;
}

.header__title {
	font-size: 24px;
	font-weight: bold;
	text-align: center;
}

/* main -------------------------------------------------------------------------------- */

.main {
	padding-bottom: 50px;
}

.main__item:not(:last-child) {
	margin-bottom: 50px;
}

.task {
	min-height: 100vh;
}

.task__header {
	display: flex;
	justify-content: center;
	align-items: center;

	padding-top: 20px;
	padding-bottom: 20px;
}

.task__title {
	margin-right: 30px;

	font-size: 20px;
	font-weight: bold;
	text-align: center;
}

.menu {
	display: flex;
	flex-direction: column;
}

.menu__item:not(:last-child) {
	margin-bottom: 10px;
}

.menu__link {
	position: relative;
	display: block;
	width: 34px;
	height: 15px;
}

.menu__link::before,
.menu__link::after {
	content: '';
	position: absolute;
	top: 5px;
	width: 20px;
	height: 4px;

	border-radius: 3px;
	background-color: lightgray;

	transition: background-color 0.3s;
}

.menu__link-bottom::before {
	left: 0;
	-webkit-transform: rotate(30deg);
	        transform: rotate(30deg);
}

.menu__link-bottom::after {
	right: 0;
	-webkit-transform: rotate(-30deg);
	        transform: rotate(-30deg);
}

.menu__link-top::before {
	left: 0;
	-webkit-transform: rotate(-30deg);
	        transform: rotate(-30deg);
}

.menu__link-top::after {
	right: 0;
	-webkit-transform: rotate(30deg);
	        transform: rotate(30deg);
}

.menu__link:hover::before,
.menu__link:hover::after {
	background-color: #a6a6a6;
}

.task__body {
	position: relative;
}

/* Общие стили */

/* stars.css */

.card-body-stars {
	display: flex;
}
/* listing.css (готовые стили) */

.task_2 {
  display: flex;
  flex-direction: column;
}

.task__body_task2 {
  flex: 1 1 auto;
  width: 100%;

  font-family: 'Work Sans', sans-serif;
  line-height:1.4;

  background:#e9e9e9;
  color:#333;
}

/* Shop Styles */

.item-list {
  grid-column-gap:1rem;
  -webkit-column-gap:1rem;
          column-gap:1rem;
  margin:1rem 0 0;
  padding:0;
  opacity: 1;
  transition: opacity 300ms ease;
}

.item {
  background-color:#fff;
  border-radius:2px;
  box-shadow:2px 2px 4px 0 #d1d1d1;
  display:inline-block;
  margin:0 0 1rem;
  padding:1rem;
  position:relative;
  width:100%;
}

.item.hidden {
  visibility:hidden;
}

.item-image {
  background-color:#e9e9e9;
}

.item-image img {
  border-radius:2px;
  display:block;
  height:auto;
  transition:opacity 1200ms ease;
  width:100%;
}

.item-title {
  font-size:.85rem;
  margin:.5rem .25rem;
  word-wrap:break-word;
}

.item-price {
  color:#3c763d;
  display:inline-block;
  font-size:1.2rem;
  font-weight:600;
  margin:.5rem .25rem 0;
  line-height:1;
}

.item-quantity {
  border-radius:2px;
  bottom:1rem;
  color:#fff;
  font-size:.75rem;
  font-weight:600;
  padding:.25rem .5rem;
  position:absolute;
  right:1rem;
}


/* Quantity Level */

[class^="level-"] {
  color:#fff;
}

.level-low { background-color:#a94442; }
.level-medium { background-color:#8a6d3b; }
.level-high { background-color:#3c763d; }


/* Media Queries */

@media only screen and (min-width:480px) {
  .item-list {
    -webkit-column-count:2;
            column-count:2;
  }
}

@media only screen and (min-width:700px) {
  .item-list {
    -webkit-column-count:3;
            column-count:3;
  }
}

@media only screen and (min-width:900px) {
  .item-list {
    -webkit-column-count:4;
            column-count:4;
  }
}

@media only screen and (min-width:1200px) {
  .item-list {
    -webkit-column-count:5;
            column-count:5;
  }
}

